import { __ } from '@wordpress/i18n';
import { InspectorControls, BlockControls } from '@wordpress/block-editor';
import { PanelBody, PanelRow, RangeControl, ToolbarGroup, ToolbarButton, __experimentalUnitControl as UnitControl, __experimentalNumberControl as NumberControl, Button, Dashicon } from '@wordpress/components';

// Settings Components
import Title from '../../Components/Title';
import BColor from '../../Components/BColor';
import BtnGroup from '../../Components/BtnGroup';

import icons from './Const/icons';
import options from './Const/options';
const { positions, pxUnit, perUnit, emUnit } = options;

const Settings = ({ attributes, setAttributes }) => {
	const { segments, waves, position, height } = attributes;

	const addWave = () => {
		setAttributes({
			waves: [...waves, {
				speed: 1.5,
				shape: 1.8,
				position: 0,
				fillColor: '#0d5cb4',
				strokeWidth: 2,
				strokeColor: '#4c9af0'
			}]
		});
	}

	const updateWave = (index, type, val) => {
		const newWaves = [...waves];

		newWaves[index][type] = val;
		setAttributes({ waves: newWaves });
	}


	return <>
		<InspectorControls>
			<PanelBody className='bPlPanelBody help' title={__('Help', 'separator')}>
				{/* <div className='helpItem'>
					<a href='https://bblockswp.com/docs/separator/' target='_blank' rel='noreferrer'><Dashicon icon='book' />{__('Read Documentation', 'separator')}</a>
				</div> */}

				<div className='helpItem rateUs'>
					<a href='https://wordpress.org/support/plugin/section-separator/reviews/#new-post' target='_blank' rel='noreferrer'>
						<span><Dashicon icon='star-filled' />{__('Would you please rate us?', 'separator')}</span>
						<span>{__('We are new and we need your help to grow!🙏', 'separator')}</span>
					</a>
				</div>
			</PanelBody>


			<PanelBody className='bPlPanelBody addRemoveItems' title={__('Add or Remove Waves', 'separator')}>
				{waves.map((item, index) => {
					const { speed, shape, position, fillColor, strokeWidth, strokeColor } = item;

					return <PanelBody key={index} className='bPlPanelBody editItem' title={__(`Wave No ${index + 1}:`, 'separator')} initialOpen={0 !== index ? false : true}>
						<Title mt='0'>{__('Speed:', 'separator')}</Title>
						<RangeControl value={speed} onChange={val => updateWave(index, 'speed', val)} min={0} max={4} step={.01} />

						<Title>{__('Shape:', 'separator')}</Title>
						<RangeControl value={shape} onChange={val => updateWave(index, 'shape', val)} min={.25} max={3} step={.01} />

						<NumberControl className='mt20' label={__('Position (from top):', 'separator')} labelPosition='left' value={position} onChange={val => updateWave(index, 'position', val)} />
						<small>{__('Min is 0, max is height of the separator')}</small>

						<BColor label={__('Fill Color:', 'separator')} value={fillColor} onChange={val => updateWave(index, 'fillColor', val)} defaultColor='#093da8' />

						<Title>{__('Stroke Width:', 'separator')}</Title>
						<RangeControl value={strokeWidth} onChange={val => updateWave(index, 'strokeWidth', val)} min={0} max={10} step={1} />

						<BColor label={__('Stroke Color:', 'separator')} value={strokeColor} onChange={val => updateWave(index, 'strokeColor', val)} defaultColor='#4c9af0' />

						<PanelRow className='itemAction mt20'>
							<Button className='removeItem' label={__('Remove', 'separator')} onClick={e => {
								e.preventDefault();
								setAttributes({ waves: [...waves.slice(0, index), ...waves.slice(index + 1)] });
							}} ><Dashicon icon='no' />{__('Remove', 'separator')}</Button>

							<Button className='duplicateItem' label={__('Duplicate', 'separator')} onClick={e => {
								e.preventDefault();
								setAttributes({ waves: [...waves, { ...waves[index] }] });
							}} >{icons.gearSettings}{__('Duplicate', 'separator')}</Button>
						</PanelRow>
					</PanelBody>
				})}

				<div className='addItem'>
					<Button label={__('Add New Wave', 'separator')} onClick={addWave}><Dashicon icon='plus' />{__('Add New Wave', 'separator')}</Button>
				</div>
			</PanelBody>


			<PanelBody className='bPlPanelBody' title={__('Separator Settings', 'separator')} initialOpen={false}>
				<Title mt='0'>{__('Wave Segments:', 'separator')}</Title>
				<RangeControl value={segments} onChange={val => setAttributes({ segments: val })} min={1} max={80} step={1} />

				<PanelRow className='mt20'>
					<Title mt='0' mb='0'>{__('Vertical Position:', 'separator')}</Title>
					<BtnGroup value={position} onChange={val => setAttributes({ position: val })} options={positions} />
				</PanelRow>

				<UnitControl className='mt20' label={__('Height:', 'separator')} labelPosition='left' value={height} onChange={val => setAttributes({ height: val })} units={[pxUnit, perUnit, emUnit]} />
			</PanelBody>
		</InspectorControls>


		<BlockControls>
			<ToolbarGroup className='bPlToolbar'>
				<ToolbarButton label={__('Add New Wave', 'separator')} onClick={addWave}><Dashicon icon='plus' /></ToolbarButton>
			</ToolbarGroup>
		</BlockControls>
	</>;
};
export default Settings;